import * as yup from 'yup';

export const signUpValidation = yup.object({
  email: yup.string().required(' '),
  nickname: yup
    .string()
    .required(' ')
    .max(10, ' 10  '),
  password: yup
    .string()
    .required(' ')
    .max(20, ' 20  ')
    .min(8, ' 8  '),
  passwordCheck: yup
    .string()
    .oneOf([yup.ref('password'), null], '  '),
  term: yup.boolean().oneOf([true], ' '),
});

export const addCommunityValidation = yup.object({
  communityName: yup
    .string()
    .required(' ')
    .max(10, '  10  '),
  description: yup
    .string()
    .required(' ')
    .max(140, ' 140 '),
});

export const createMeetValidation = yup.object({
  title: yup
    .string()
    .required('  ')
    .max(15, ' 15 '),
  fee: yup.number().min(0).required(' ( 0)'),
  members: yup
    .number()
    .min(1, ' 1 ')
    .required('  '),
  date: yup.date().required(' '),
});
